import React, { Component } from 'react';
import {HashRouter, Link} from 'react-router-dom';
import AboutMe from '../AboutMe/AboutMe.js'
import './getNavBar.css';

class NavBar extends Component {
    render() {
        const sections = [{name:"Home", className:"home"}, {name:"About Me", className:"about-me"},
        {name:"Skills", className:"skills"},  {name:"Projects", className:"projects"},
        {name:"Contact", className:"contact"}];
        const navLinks = sections.map(section => {
            return (<li><a href={'#' + section.className} className={section.name} ><Link to={"#/" + section.className}/>
            {section.name}</a></li>);
        })
        return (
            <HashRouter>
            <nav className="topnav" id="myTopnav">
                    <div className="iconholder"> 
                    <h7 className="navheading">{this.props.title}</h7> 
                    
                        <script type="text/javascript" src="https://platform.linkedin.com/badges/js/profile.js" async defer></script>
                        <a href='https://uk.linkedin.com/in/penelopepoppycurtis?trk=profile-badge' target="_blank">
                            <img className="icon" src="data:image/svg+xml;base64," /></a>
                        <a href="https://github.com/PoppyCurtis" target="_blank">
                            <img className="icon" src="data:image/svg+xml;base64," /></a>
                        <a href="mailto:poppy.curtis@gmail.com" target="_blank">
                            <img className="icon" src="data:image/svg+xml;base64,"></img></a>
                    </div>
                <ul>
                    {navLinks}
                </ul>
            </nav>
            </HashRouter>
        );
    }
}

export default NavBar;